ReactAudioPlayer {
  background-color: #C2E2C0;
}

.rhap_main-controls svg path {
  fill: #014441;
}

.rhap_progress-indicator {
  background-color: #014441;
}

.rhap_progress-bar {
  background-color: #d9eee4;
}

.rhap_progress-filled {
  background-color: #014441;
}

.progress-bar {
  display: flex;
  flex-direction: row;
  justify-content: space-between;
  align-items: center;

  padding: 20px;

  @media (max-width: 1000px) {
    flex-direction: column;
  }

  &__link {
    font-size: 24px;
    color: #fff;
    padding: 10px 60px;
    background-color: #228B22;
    border-radius: 10px;

    @media (max-width: 1000px) {
      padding: 10px 30px;
    }
    
    &:hover {
      background-color: #C2E2C0;
    }
    
    &:active {
      background-color: #A2D1A1
    }
  }

  &__title {
    font-size: 36px;
    color: #000;
  }

  &__progress {
    &__level {
      &__text {
        font-size: 24px;
        color: #000;
      }
      
      &__container {
        position: relative;

        &__empty {
          width: 100%;
          height: 15px;
          border-radius: 20px;
          background-color: #A2D1A1;
        }

        &__fill {
          background-color: #01252A;
          height: 15px;
          border-radius: 20px;
          position: absolute;
          top: 0;
          left: 0;

          @for $end from 1 through 100 {
            &--#{$end} {
              width: calc(#{$end} * 1%);
            }
          }
          
        }
      }
    }

    &__volume {
      &__text {
        font-size: 24px;
        color: #01252A;
      }
      &__container {
        position: relative;
        border-radius: 10px;

        &__empty {
          width: 100%;
          height: 15px;
          background-color: #A2D1A1;
          border-radius: 20px;
        }

        &__fill {
          background-color: #01252A;
          height: 15px;
          border-radius: 20px;
          position: absolute;
          top: 0;
          left: 0;

          @for $end from 1 through 100 {
            &--#{$end} {
              width: calc(#{$end} * 1%);
            }
          }
          
        }
      }
    }
  }
}

.button {
  font-size: 24px;
  color: #fff;
  padding: 10px 60px;
  background-color: #228B22;
  border-radius: 10px;
  
  &:hover {
    background-color: #C2E2C0;
  }
  
  &:active {
    background-color: #A2D1A1
  }
}

.form {
  display: flex;
  flex-direction: column;
  gap: 20px;
  justify-content: center;
  align-items: center;
  
  padding: 10px;
  &_section {
    display: flex;
    flex-direction: column;
    gap: 20px;
    justify-content: center;
    align-items: center;

    &__question {
      display: flex;
      flex-direction: row;
      justify-content: flex-start;
      align-items: center;
      gap: 20px;

      @media (max-width: 1000px) {
        flex-direction: column;
        align-items: center;
        justify-content: center;
      }

      &__answers {
        display: flex;
        flex-direction: row;
        justify-content: flex-start;
        align-items: center;
        gap: 20px;

        @media (max-width: 430px) {
          flex-direction: column;
          align-items: flex-start;
          justify-content: center;
          gap: 10px;
        }

        &__wrong {
          background-color: rgb(245, 187, 187);
          border-radius: 30px 0 0 30px;
        }
  
        &__right {
          background-color: rgb(195, 241, 195);
          border-radius: 30px 0 0 30px;
        }
      }

      &__wrong {
        background-color: rgb(245, 187, 187);
        border-radius: 30px 0 0 30px;
      }

      &__right {
        background-color: rgb(195, 241, 195);
        border-radius: 30px 0 0 30px;
      }
    }
  }

  &_title {
    padding: 20px;

    &__color {
      &--green {
        color: green;
      }

      &--red {
        color: red;
      }

      &--yellow {
        color: yellow;
      }

      &--blue {
        color: blue;
      }
    }
  }
}
.test__link_div {
  display: flex;
  flex-direction: row;
  justify-content: center;
  align-items: center;
}
.test__link {
  font-size: 24px;
  color: #fff;
  padding: 10px 60px;
  margin: 10px auto;
  background-color: #228B22;
  border-radius: 10px;
  
  &:hover {
    background-color: #C2E2C0;
  }
  
  &:active {
    background-color: #A2D1A1
  }
}



.rhap_button-clear.rhap_repeat-button {
  display: none;
}

.rhap_volume-controls {
  display: none;
}

.image-option {
  height: 100px;
  width: 100px;
  object-fit: cover;
}
