.progress-bar--settings {
  flex-direction: column;
  justify-content: space-between;
  align-items: center;
}

.border {
  border: 1px solid #014441;
  padding: 20px 30px;
  display: flex;
  gap: 10px;
  justify-content: center;
  align-items: center;
  cursor: pointer;
}
.userIdSection {
  display: flex;
  gap: 10px;
  justify-content: center;
  align-items: center;
}

.userIdLoading {
  display: flex;
  gap: 10px;
  justify-content: center;
  align-items: center;
}
