.section {
  &--adv {
    display: flex;
    flex-direction: row;
    justify-content: space-between;
    align-items: center;
    gap: 15px;

    .section__image {
      width: 30%;

      @media (max-width: 1200px) {
        display: none;
      }
    }
  }

  &--about.section--main {
    background-color: #C2E2C0;
    background-image: url('../../images/background_about.png');
    background-size: cover;
    background-repeat: no-repeat;
    background-origin: border-box;
    background-position: left top;

    @media (max-width: 1000px) {
      background-image: url('../../images/background_about_.png');
      background-position: left top;
    }
  }

  &__title {
    margin-top: 20px;
    margin-bottom: 20px;
    font-size: 46px;
    color: #000000;
    text-align: center;

    &--main {
      font-size: 52px;
      text-align: left;
    }
  }

  &__text {
    font-size: 26px;
    color: #000000;

    &--left {
      width: 40%;

      @media (max-width: 1000px) {
        width: 70%;
      }

      @media (max-width: 500px) {
        width: 100%;
      }
    }
  }

  &__tests {
    margin-top: 40px;
    padding-bottom: 20px;
    margin-bottom: 20px;

    &__link {
      min-width: 50%;
      font-size: 24px;
      color: #fff;
      padding: 10px 60px;
      background-color: #228B22;
      border-radius: 10px;
      display: block;
      text-align: center;
      margin: 5px auto;
      
      &:hover {
        background-color: #C2E2C0;
      }
      
      &:active {
        background-color: #A2D1A1
      }
    }
  }

  &__test {
    padding: 40px;
    box-sizing: border-box;
    border: solid 2px #228B22;
    margin-bottom: 25px;
    border-radius: 0 0 50px 0;

    background-size: contain;
    background-repeat: no-repeat;
    background-position: right center;

    @media (max-width: 1000px) {
      background-image: none;
    }

    @media (max-width: 435px) {
      padding: 20px;
    }

    &__title {
      margin-bottom: 20px;
      font-size: 36px;
      color: #000000;
      @media (max-width: 435px) {
        font-size: 32px;
      }
    }
  
    &__description {
      font-size: 24px;
      color: #000000;
      // width: 85%;
      margin-bottom: 25px;

      @media (max-width: 435px) {
        font-size: 20px;
      }
    }
    
    &__link {
      font-size: 24px;
      color: #fff;
      padding: 10px 60px;
      background-color: #228B22;
      border-radius: 10px;

      @media (max-width: 435px) {
        font-size: 20px;
        padding: 10px 15px;
      }
      
      &:hover {
        background-color: #C2E2C0;
      }
      
      &:active {
        background-color: #A2D1A1
      }
    }
  }
}
