.section__tests-card {
  display: flex;
  flex-direction: row;
  justify-content: space-around;
  align-items: center;
  flex-wrap: wrap;
  gap: 15px;

  margin-top: 30px;
  margin-bottom: 40px;
  padding-bottom: 30px;
}

.section__test-card {
  height: 550px;
  width: 300px;
  box-sizing: border-box;
  padding: 10px 10px 20px;

  border-radius: 0 0 50px 0;
  border: solid 2px #228B22;

  display: flex;
  flex-direction: column;
  justify-content: space-between;

  &__title {
    font-size: 30px;
    color: #000;
  }

  &__description {
    font-size: 20px;
    color: #000;
    // width: 85%;
    margin-bottom: 25px;
  }
  
  &__link {
    font-size: 24px;
    color: #fff;
    padding: 10px 30px;
    background-color: #228B22;
    border-radius: 10px 10px 30px 10px;
    
    justify-self: flex-end;

    &:hover {
      background-color: #C2E2C0;
    }
    
    &:active {
      background-color: #A2D1A1
    }
  }
}
